.about_me .profile_pic {
  display: block;
  margin: auto;
}

.about_me .name {
  font-weight: 500;
  font-size: 24px;
  line-height: 28px;
  text-align: center;
}

.about_me .quals-under-name {
  font-weight: 300;
  font-size: 13px;
  line-height: 16px;
  text-align: center;
  width: 70%;
  margin: auto;
  margin-top: -7px;
  margin-bottom: 18px;
  letter-spacing: 1px;
}

.contact-div {
  display: inline-block;
  width: 25%;
  text-align: center;
  vertical-align: top;
  padding: 15px;
}

.contact-div i {
  display: block;
  width: 40px;
  height: 40px;
  background: #fccd12;
  border-radius: 50%;
  padding: 12px 8px;
  margin: auto;
  margin-bottom: 15px;
  color: #000;
}

.contact-div p,
.contact-div a {
  margin-bottom: 0;
  text-align: center;
  color: #fff;
  font-weight: 300;
}

@media (max-width: 575px) {
  .contact-div {
    width: 100%;
  }
}

@media (min-width: 576px) and (max-width: 992px) {
  .contact-div {
    width: 50%;
  }
}
