.dark_theme {
  background: #333333;
  color: #fff;
  padding: 50px 15px;
}

.light_theme {
  background: #f8f8f8;
  color: #333333;
  padding: 50px 15px;
}

.yellow_theme {
  background: #fccd12;
  color: #000;
  padding: 50px 15px;
}

.timeline-table tr td:nth-child(1) {
  width: 100px;
}

.timeline-table tr td {
  padding-top: 35px;
  vertical-align: top;
}

.timeline-table .qual {
  font-weight: 500;
  font-size: 16px;
  line-height: 18px;
  margin-bottom: 5px;
}

.timeline-table .institute {
  font-weight: 300;
  font-size: 16px;
  line-height: 18px;
  margin-bottom: 0;
}

.demo_link {
  display: block;
  color: #67fbfb;
}

.demo_link a {
  color: #fff;
  font-weight: 300;
}

.demo_link_project {
  display: block;
  color: #000;
}

.demo_link_project a {
  font-weight: 300;
}
